.body {
    /* display: flex;
    justify-content: center;
    align-items: center; */
    height: 100vh;
    width: 80vw;
    margin: 0;
    margin-left: 4%;
    color: white;
    background-color: white;
    font-family: Arial, sans-serif;
  }

  .center-div {
    width: 70vw;
    height: 100vh;
    background-color: rgb(102, 71, 255);
    border-radius: 15px;
    padding: 5px;
    box-sizing: border-box;
    color: white;
    display: flex;
    justify-content: left;
    align-items: left;
    text-align: left;
  }

