*{
  margin: 0%;
  padding: 0%;
  overflow-y: hidden;
}

#chatbot-box-mobile{
  display: none;
}
.row {
   --bs-gutter-x: 0%;
}
.nav-link{
  color: black;
}

 #chatbot-box .heading-menu{
  background-color:whitesmoke;
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: black;
  height: 100vh;
}
 .nav-link:hover,.nav-link:active,.nav-link::after {
  background-color:darkslategray; 
  color:white;
}


@media screen and (max-width: 700px) {
  #chatbot-box{
      display: none;
  }

  #chatbot-box-mobile div #hello{
    text-align: center;
    overflow-y: hidden;
  }

  #chatbot-box-mobile div #hello img{
    width: 50%;
    mix-blend-mode: darken;
  }
.floating-button{
  position: absolute;
  float: right;
  right: 3%;
  top: 11%;
}
  #chatbot-box-mobile {
      display: block;    
  }
  .floating-button .btn-color{
   padding: 1px 20px;
   padding-top: 2px;
   border: none;
   border-bottom-left-radius: 10px;
   border-bottom-right-radius: 10px;
   
    background: darkslategray;
  }
  #chatbot-box-mobile button{
    color: black;
  }
  #chatbot-box-mobile button:hover,#chatbot-box-mobile button:active{
    color: white;
  }
  #chatbot-box-mobile a{
    position: absolute;
    
    background-color: #2b313f;
  }
  
}